import PrivateSaleAbi from '../contracts/PrivateSale.json';
import { useContract } from './useContract';

export function usePrivateSaleApi() {
  const contractAddress = PrivateSaleAbi.address;
  const { functions } = useContract(contractAddress, PrivateSaleAbi.abi);
  return {  
    isOwner: functions.isOwner,

    setStartTime: functions.setStartTime,
    setEndTime: functions.setEndTime,

    getStartTime: functions.getStartTime,
    getEndTime: functions.getEndTime,
    getContributors: functions.getContributors,

    joinPool: functions.joinPool,
    withdraw: functions.withdraw,
  };
}
