import React, { Component } from "react";

import ComingCard from "./ComingCard";

import { projects } from "../../assets/variables";
import { privateComingPool } from "../../assets/variables";
import rightsidemamba from "../../assets/img/rightsidemamba.svg";

export default class ProjectsComing extends Component {
  render() {
    return (
      <>
        <section className="container projects-coming ">
          <h3 className="col-12 text-center font-weight-bold sub-title">
            PROJECTS COMING SOON
          </h3>
          <section className="coming-projects">
              <ComingCard  project={privateComingPool} />
          </section>
          <img
            src={rightsidemamba}
            alt="rightmamba"
            className="coming-projects-mamba"
          />
        </section>
        <hr style={{ border: "1px solid #fdac42" }} />
      </>
    );
  }
}
