import React, { useState, useEffect } from "react";
import { Row, Col, Table, Form } from "react-bootstrap";

import Countdown from "react-countdown";
import SocialLinkinCard from "../SocialLinkinCard";
import PropTypes from "prop-types";

import { usePrivateSaleApi } from "../../hooks/usePrivateSaleApi";
import { useAccount } from "../../hooks/useAccount";

export default function PrivateCard({ pool }) {
  const [amount, setAmount] = useState();
  const [contribution, setContribution] = useState();
  const [isOpen, setIsOpen] = useState(false);
  const [endTime, setEndTime] = useState(pool.startTime * 1.0);
  const [balanceBNB, setBalanceBNB] = useState("");
  const [balanceMAMP, setBalanceMAMP] = useState("");
  const decimal = 100000000;

  const { joinPool, withdraw, getContributors } = usePrivateSaleApi();
  const { account, getBalanceBNB, getBalanceMAMP } = useAccount();

  useEffect(() => {
    getBalanceBNB().then(setBalanceBNB);
    getBalanceMAMP().then(setBalanceMAMP);
  }, [account, getBalanceBNB, getBalanceMAMP]);

  // console.log(account)
  // console.log(balanceBNB, balanceMAMP)

  const startTimeEpoch = new Date(pool.startTime) * 1.0;
  const endTimeEpoch = new Date(pool.endTime) * 1.0;
  const duration = endTimeEpoch - startTimeEpoch;
  const start_Time = new Date(pool.startTime * 1.0).toUTCString();
  const end_Time = new Date(pool.endTime * 1.0).toUTCString();
  const timeOut = startTimeEpoch - Date.now();
  const getLocalTime = (s) => localStorage.getItem(s) * 1.0;

  setTimeout(() => {
    // console.log("now is the time");
    setIsOpen(true);
  }, timeOut);

  const handleAmount = (e) => {
    const value = e.target.value * 1.0;
    console.log(typeof value, value);
    setAmount(value);
    setContribution(value * 100000);
  };
  const handleContribution = (e) => {
    const value = e.target.value * 1.0;
    setContribution(value);
    setAmount(value / 100000);
  };

  const fetchContributors = async() => {
    try{
      const contributors =  await getContributors();
      console.log(contributors)
    } catch(err) {
      console.log(err)
    }
  }

  const jointoPool = async () => {
    try {
      console.log(typeof amount, amount);
      await joinPool(account, amount * decimal, contribution * decimal);
      console.log("success");
      fetchContributors();
    } catch (err) {
      console.log(err);
    }
  };

  const withdrawMamp = async () => {
    try {
      await withdraw();
      console.log("success");
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    const savedDate = getLocalTime("end_date");
    setEndTime(savedDate);
    if (savedDate === null || savedDate === Date.now() || savedDate === 0) {
      // console.log(savedDate);
      const date = pool.open_time * 1.0;
      setEndTime(date);
      localStorage.setItem("end_date", date);
    }
  }, []);

  const AfterCountdown = () => {
    // console.log("i am afterconutdonw");
    return <span>You are available.</span>;
  };

  return (
    <>
      {isOpen && (
        <section className="pool-card">
          <div className="pool-card-header">
            <a href="/">
              <img src={pool.picture} alt="pool profile"></img>
            </a>
            <div className="pool-card-title">
              <strong>{pool.name}</strong>
              <SocialLinkinCard />
            </div>
            <p style={{ color: "orange" }}></p>
          </div>
          <div className="pool-card-body">
            <section>
              <h4 style={{ marginBottom: 10, textAlign: "center" }}>
                Total Amount per IDO: {pool.total_amount} BNB{" "}
              </h4>
              <h4 style={{ marginBottom: 10, textAlign: "center" }}>
                Pool OPEN: {pool.startTime}
              </h4>
              <h4 style={{ marginBottom: 0, textAlign: "center" }}>
                Pool will be open for {duration} hours
              </h4>
              <Row style={{ marginTop: "20px" }}>
                <Col lg={7} style={{ marginTop: "10px" }}>
                  <Table
                    className="allocation-table"
                    bordered
                    hover
                    variant="dark"
                  >
                    <thead>
                      <tr>
                        <th colspan="2">ALLOCATION(BNB)</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>MIN</td>
                        <td>{pool.min_allocation}</td>
                      </tr>
                      <tr>
                        <td>MAX</td>
                        <td>{pool.max_allocation}</td>
                      </tr>
                    </tbody>
                    <thead>
                      <tr>
                        <th colspan="2">PRICE(USDT)</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>IDO</td>
                        <td>{pool.ido_price}</td>
                      </tr>
                      <tr>
                        <td>LISTING</td>
                        <td>{pool.listing_price}</td>
                      </tr>
                    </tbody>
                  </Table>
                </Col>
                <Col lg={5} style={{ padding: "0 30px" }}>
                  <h3 style={{ marginBottom: 0, fontSize: "14px" }}>
                    AMOUNT(BNB)
                  </h3>
                  <Form.Control
                    style={{ marginBottom: "20px" }}
                    type="number"
                    value={amount}
                    onChange={handleAmount}
                  />
                  <div className="join-button">
                    <button class="poolcard-button" onClick={jointoPool}>
                      JOIN POOL
                    </button>
                  </div>
                  <h3
                    style={{
                      marginBottom: 0,
                      marginTop: "70px",
                      fontSize: "14px",
                    }}
                  >
                    CONTRIBUTION(MAMP)
                  </h3>
                  <Form.Control
                    value={contribution}
                    type="number"
                    onChange={handleContribution}
                  />
                </Col>
              </Row>
              <Row style={{ marginTop: "30px" }}>
                <Col lg={7}>
                  <h3 style={{ marginBottom: 0, fontSize: "14px" }}>
                    COUNTDOWN
                  </h3>
                  <div style={{ fontSize: "2em" }}>
                    <Countdown
                      date={
                        getLocalTime("end_date")
                          ? getLocalTime("end_date")
                          : endTime
                      }
                      autoStart={() => {
                        localStorage.removeItem("end_date");
                      }}
                      onComplete={() => {
                        localStorage.removeItem("end_date");
                      }}
                      daysInHours
                    >
                      <AfterCountdown />
                    </Countdown>
                  </div>
                </Col>
                <Col lg={5} style={{ padding: "0 30px" }}>
                  <h3 style={{ marginBottom: 0, fontSize: "14px" }}>
                    MAMP TOKEN
                  </h3>
                  <button class="poolcard-button" onClick={withdrawMamp}>
                    CLAIM
                  </button>
                </Col>
              </Row>
            </section>
          </div>
        </section>
      )}
    </>
  );
}

PrivateCard.propTypes = {
  pool: PropTypes.object.isRequired,
};
