import React from "react";
import {
    SiWebpack,
    AiFillTwitterCircle,
    AiOutlineMedium,
    FaTelegramPlane,
  } from "react-icons/all";

import PropTypes from "prop-types";

const SocialLinkinCard = ({ size }) => {
  return (
    <div className="social-links">
      <a href="https://twitter.com/mambapad_ido">
        <AiFillTwitterCircle className="social-link" />
      </a>
      <a href="https://medium.com/@mambapad">
        <AiOutlineMedium className="social-link" />
      </a>
      <a href="https://t.me/mambapad_official">
        <FaTelegramPlane className="social-link" />
      </a>
      <a href="https://t.me/mambapad_ann">
        <FaTelegramPlane className="social-link" />
      </a>
    </div>
  );
};

SocialLinkinCard.propTypes = {
  size: PropTypes.number.isRequired,
};

export default SocialLinkinCard;
