// import { JsonRpcProvider } from '@ethersproject/providers';
import { formatEther } from '@ethersproject/units';
import { useWeb3React } from '@web3-react/core';
import { getMambapadContract } from "../utils/contractHelpers";

export function useAccount() {
  const { account, library } = useWeb3React();

  const getBalanceBNB = async () => {
    const balance = await library?.getBalance(account);
    return balance && formatEther(balance);
  };

  const getBalanceMAMP = async () => {
    const balance = await getMambapadContract(library.getSigner()).balanceOf(account);
    return balance && formatEther(balance);
  };

  return { account, getBalanceBNB, getBalanceMAMP };
}
