import React from "react";

import PropTypes from "prop-types";

import {
  SiWebpack,
  AiFillTwitterCircle,
  AiOutlineMedium,
  FaTelegramPlane,
} from "react-icons/all";

export default function ClosedCard({ project }) {
  const endTime = new Date(1643241600000).toUTCString();

  return (
    <section className="closed-card">
      <div className="closed-card-header">
        <a href="/">
          <img src={project.picture} alt="project profile"></img>
        </a>
        <div className="closed-card-title">
          <strong>{project.name}</strong>
          <div className="social-links">
            <a href="https://www.google.com">
              <SiWebpack className="social-link" />
            </a>
            <a href="https://www.twitter.com">
              <AiFillTwitterCircle className="social-link" />
            </a>
            <a href="https://www.medium.com">
              <AiOutlineMedium className="social-link" />
            </a>
            <a href="https://www.telegram.com">
              <FaTelegramPlane className="social-link" />
            </a>
          </div>          
        </div>
      </div>
      <div className="closed-card-body">
        {project.message}        
      </div>
      <div className="closed-card-footer">
        <strong>This Pool was closed at </strong>
        <strong>{endTime}</strong>
      </div>
    </section>
  );
}

ClosedCard.propTypes = {
  project: PropTypes.object.isRequired,
};
